@import "variables-fonts";

@font-face {
  font-family: 'icomoon';
  src: url('@{icomoon-font-path}/icomoon.ttf?ns7l15') format('truetype'),
  url('@{icomoon-font-path}/icomoon.woff?ns7l15') format('woff'),
  url('@{icomoon-font-path}/icomoon.svg?ns7l15#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}

.icon-list-options {
  &:empty {
    display: none;
  }
  span {
    padding: 0 !important;
    text-align: center;
    outline: 0 !important;
    font-size: 1em;
    line-height: 1.3333333;
    display: inline-block;
    vertical-align: middle;
    white-space: nowrap;
    margin-bottom: 1.5em;
    margin-top: 1.5em;
    margin-right: 2em;
    max-width: 80px;
    &:last-child {
      margin-right: 0;
    }
    span {
      display: block;
      text-align: center;
      margin: 0;
    }
    i {
      font-size: 3em;
      position: relative;
      line-height: 45px;
    }
  }

}

